.metric-name {
  cursor: pointer;
}

.treetable-node-name {
  cursor: pointer;
}

.treetable-node.locality {
  font-weight: bold;
}

.treetable-node.hovered {
  background-color: rgb(213, 235, 241);
}

.treetable {
  width: 1000px;
}

.treetable thead {
  background-color: #f5f5f5;
  border-bottom: 1px solid lightgrey;
}

.treetable th {
  padding: 5px;
}

td.treetable-stat {
  padding-right: 5px;
  padding-left: 5px;
  text-align: right;
}

td.treetable-stat:hover {
  cursor: pointer;
  color: blue;
  text-decoration: underline;
}

.treetable td, .treetable th {
  border-left: 1px solid lightgrey;
  border-right: 1px solid lightgrey;
}

.treetable {
  border-top: 1px solid lightgrey;
  border-bottom: 1px solid lightgrey;
}



